import React, { Component } from "react";
import "./About.css";
import { Details } from "./Details";

class About extends Component {
  render() {
    return (
      <div className="About" id="about">
        <div className="container">
          <div className="about-image">
            <img
              src="https://avatars.githubusercontent.com/u/110049484?v=4"
              alt="Charchit"
              height="300"
              width="300"
            />
          </div>
          <div className="about-section">
            <h1 className="about-header">
              About Me
              <hr className="divider" />
            </h1>

            <h3 className="about-intro">
            A passionate Full Stack Web Developer, Dedicated to developing
            robust applications that interact with the part of web applications.
            Extensive expertise in website architecture, design, and web design,
            and web API designs. Problem-solving mindset with a goal to optimize
            my application for better stability and speed. Love to work in a
            team.
            </h3>
            <br />
            <ul className="about-details">
              {Details.map((item, index) => {
                return (
                  <li className="about-details-list" key={index}>
                    <span className="about-title">{item.title} </span>
                    <span className="title.value">{item.value} </span>
                  </li>
                );
              })}
            </ul>
          </div>
        </div>
      </div>
    );
  }
}

export default About;
